/**
 * Copy
 */

p {
  margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  max-width: 100%;
  overflow: auto;
  margin-bottom: 1.6em;
  padding: 1.6em;

  background: color( background-pre );

  font-family: monospace;
  font-size: 0.9375em;
  line-height: 1;
}

code,
kbd,
tt,
var {
  font-family: monospace;
  font-size: 0.9375em;
}

abbr,
acronym {
  border-bottom: 1px dotted color( border-abbr );
  cursor: help;
}

mark,
ins {
  background: color( background-ins );
  text-decoration: none;
}

big {
  font-size: 125%;
}

.section-title {
  text-align: center;
  letter-spacing: 0.125em;
  font-weight: 800;

  text-shadow: -1px -1px 0 rgba( color( red ), 0.95 );

  @include media( $third-break ) {
    //font-size: em( 17, 16 );
  }
}

.entry-header {

  a {
    text-decoration: none;

    &,
    &:hover {
      color: inherit;
    }
  }

  .entry-meta {

    .post-category {
      &:after {
        content: ' / ';

        color: color( grey );
      }

      &:first-child {
        color: color( red );
      }
    }

    .posted-on {
      color: grey;
    }
  }
}
